import {ModalChangePayload} from '@here/hds-modal';
import {StepStatePayload} from '@here/hds-stepper';
import * as React from 'react';
import {Wizard, WizardActionPayload} from '@here/hds-wizard';
import {useEffect, useImperativeHandle, useRef} from 'react';
import {HDSModalProperties} from '../Modal/HDSModal.js';

// eslint-disable-next-line no-new
new Wizard();

interface HDSWizardProperties
  extends Omit<HDSModalProperties, 'onThirdButton' | 'titleSize' | 'hideThirdButton'> {
  stepper?: React.ReactNode;
  stepperFooter?: React.ReactNode;
  // eslint-disable-next-line no-unused-vars
  onStepChange?: (stepState: StepStatePayload) => void;
  stepperHeading?: string;
  stepText?: string;
}

const HDSWizard = React.forwardRef((props: HDSWizardProperties, ref) => {
  const {
    header,
    body,
    footer,
    stepper,
    stepperFooter,
    onClose,
    onConfirm,
    onCancel,
    onStepChange,
    onChange,
    className,
    hideCloseBtn,
    disableCloseOnOverlayClick,
    disableCloseOnEscape,
    closeAriaLabel,
    headingMessage,
    message,
    hideHeader,
    hideFooter,
    confirmVariant,
    hideConfirm,
    hideCancel,
    confirmText,
    cancelText,
    stepText,
    stepperHeading,
    children,
    ...rest
  } = props;

  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleClose = (e: CustomEvent<ModalChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onClose && onClose(e.detail);
    onChange && onChange(e.detail);
  };
  const _handleConfirm = () => onConfirm && onConfirm();
  const _handleCancel = () => onCancel && onCancel();
  const _handleAction = (e: CustomEvent<WizardActionPayload>) => {
    if (e.target !== innerRef.current) return;
    if (e.detail.from === 'confirm') {
      _handleConfirm();
    } else {
      _handleCancel();
    }
  };
  useEffect(() => {
    if (innerRef.current) {
      (onClose || onChange) &&
        innerRef.current.addEventListener('change', _handleClose as EventListener);
      (onConfirm || onCancel) &&
        innerRef.current.addEventListener('action', _handleAction as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleClose as EventListener);
      innerRef.current?.removeEventListener('action', _handleAction as EventListener);
    };
  }, [onClose, onChange, onConfirm, onCancel, onStepChange, innerRef]);

  const attributes: HDSWizardProperties = {};
  hideCloseBtn && (attributes['hide-close-btn'] = true);
  disableCloseOnOverlayClick && (attributes['disable-close-on-overlay-click'] = true);
  disableCloseOnEscape && (attributes['disable-close-on-escape'] = true);
  closeAriaLabel && (attributes['close-aria-label'] = closeAriaLabel);
  headingMessage && (attributes['heading-message'] = headingMessage);
  message && (attributes.message = message);
  hideHeader && (attributes['hide-header'] = hideHeader);
  hideFooter && (attributes['hide-footer'] = hideFooter);
  confirmVariant && (attributes['confirm-variant'] = confirmVariant);
  hideConfirm && (attributes['hide-confirm'] = hideConfirm);
  hideCancel && (attributes['hide-cancel'] = hideCancel);
  confirmText && (attributes['confirm-text'] = confirmText);
  cancelText && (attributes['cancel-text'] = cancelText);
  stepText && (attributes['step-text'] = stepText);
  stepperHeading && (attributes['stepper-heading'] = stepperHeading);

  return (
    <hds-wizard ref={innerRef} {...attributes} {...(className && {class: className})} {...rest}>
      {header && <div slot="header">{header}</div>}
      {body ? <div slot="body">{body}</div> : children}
      {footer && <div slot="footer">{footer}</div>}
      {stepper && <div slot="stepper">{stepper}</div>}
      {stepperFooter && <div slot="stepper-footer">{stepperFooter}</div>}
    </hds-wizard>
  );
});

export default HDSWizard;
